@import url(https://fonts.googleapis.com/css?family=Lato:300,400,700);
@import url(https://fonts.googleapis.com/css?family=Anton);
.Navbar {
    display: flex;
    background-color: black;
    justify-content: center;
    color: white;
    width: 100%;
    padding: 20px;
    font-family: 'Lato';
}

.nav {
    max-width: 1080px;
    list-style: none;
    display: flex;
    justify-content: space-evenly;
    width: 100%;
    align-items: center;
}

.nav li {
    display: flex;
    justify-content: space-evenly;
    align-self: flex-end;
    font-size: 20px;
}

.nav a {
    padding: 7px;
    border-radius: 25px;
    color: white;
    text-decoration: none;
    border: 2px solid black;
    -webkit-transition-property: all;
    transition-property: all;
    -webkit-transition-duration: 0.5s;
            transition-duration: 0.5s;
    -webkit-transition-timing-function: ease;
            transition-timing-function: ease;
}

.Navbar__Options {
    margin-bottom: 5px;
}

.logo {
    display: flex;
    width: 125px;
}

.nav a:hover {
    border: 2px solid white;
}

.Navbar__Social {
    display: flex;
    position: absolute;
    top: 35px;
    font-size: 30px;
    width: 100%;
    max-width: 980px;
    justify-content: flex-end;
}

.Navbar__Social div {
    margin: 0 5px;
}

.icon {
    -webkit-transition-timing-function: ease-in-out;
            transition-timing-function: ease-in-out;
    -webkit-transition-duration: 0.3s;
            transition-duration: 0.3s;
    -webkit-transition-property: all;
    transition-property: all;
    border-radius: 25px;
}

.icon:hover {
    background-color: white;
}
.homepage__container {
    display: flex;
    width: 100%;
    background-color: black;
    justify-content: space-evenly;
    /* border-bottom: 10px solid white;
    border-style: double; */
}

.homepage__container:before {
    content: "";
}

body {
    background-color: black;
}

.hero__mario {
    background-image: url(/insomnia/static/media/concierto1.fa7aab56.jpg);
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: -70px 20px;
    width: 50%;
    padding: 35vh;
    -webkit-clip-path: polygon(0 0, 0 100%, 60% 100%, 100% 0);
            clip-path: polygon(0 0, 0 100%, 60% 100%, 100% 0);
    border: 1px solid black;
}

.hero__mario:before {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
    background: -webkit-gradient(linear, left top, left bottom, from(transparent), to(black));
    background: linear-gradient(transparent, black);
}

.hero__mario_text {
    width: 50%;
    color: white;
    font-family: 'Lato';
}

.title {
    padding: 20px;
}

.content {
    margin-left: -50px;
    margin-right: 100px;
    text-align: justify;
    font-size: 22px;
}
.SpotifyButton__container {
    display: flex;
    text-decoration: none;
    align-items: center;
    justify-self: flex-end;
    width: 200px;
    margin-top: 30px;
    border: 2px solid #1ed760;
    border-radius: 30px;
}

.SpotifyButton__icon {
    font-size: 43px;
}

.SpotifyButton__text {
    margin-left: 10px;
    color: #1ed760;
    font-family: 'Lato';
    font-size: 20px;
}
@font-face {
    font-family: 'icomoon';
    src: url(/insomnia/static/media/icomoon.ff271db3.eot);
    src: url(/insomnia/static/media/icomoon.ff271db3.eot#iefix) format('embedded-opentype'), url(/insomnia/static/media/icomoon.f96d4615.ttf) format('truetype'), url(/insomnia/static/media/icomoon.fae9e717.woff) format('woff'), url(/insomnia/static/media/icomoon.417f914b.svg#icomoon) format('svg');
    font-weight: normal;
    font-style: normal;
    font-display: block;
}

[class^="icon-"], [class*=" icon-"] {
    /* use !important to prevent issues with browser extensions that change fonts */
    font-family: 'icomoon' !important;
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-feature-settings: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.icon-menu:before {
    content: "\e903";
}

.icon-instagram:before {
    content: "\e900";
    color: #e4405f;
}

.icon-spotify:before {
    content: "\e901";
    color: #1ed760;
}

.icon-facebook:before {
    content: "\e902";
    color: #1877f2;
}

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
